}


.sidebar {
	position: relative;
	width: 450px;
	height: 100vh;
	background-color: white;
    -ms-flex: none;
        flex: none;
    z-index: 100;
	overflow-y: auto;
	-webkit-transition: width 2s;
}


.sidebar-container {
    display: inline-block;
	padding: 40px 40px 0 40px;
    width: 100%;
}



.main {
	position: relative;
	width: calc(100vw - 400px);
	height: 100vh;
