	border-left: 1.5px solid #fff;
	border-right: 1.5px solid #fff;
}

.button-view:hover {
	background: rgb(41, 75, 116);
	color: white;
	-webkit-transition:800ms ease all;
	-o-transition:800ms ease all;
	transition:800ms ease all;
}

.button-view-activate {
