/*Use my location CSS style*/
.use-my-loc {
	cursor: pointer;
	position: relative;
	display: inline-block;
	border-bottom: 1px dotted black;
}

.use-my-loc .tooltiptext {
	visibility: hidden;
	width: 130px;
	background-color: black;
	color: #fff;
	text-align: center;
	border-radius: 6px;
	padding: 6px 0 6px 0;
	font-size: 10px;
	font-weight: lighter;
	/* Position the tooltip */
	position: absolute;
	z-index: 1;
	width: 120px;
    bottom: 100%;
    left: 50%;
    margin-left: -60px; /* Use half of the width (120/2 = 60), to center the tooltip */
}

.use-my-loc:hover .tooltiptext {
    visibility: visible;
}



